
.findrecommend {
  position: fixed;
  background: #fff;
  overflow: auto;
  top: 86px;
  bottom: 46px;
  left: 0;
  right: 0;
  z-index: 11;
  background: #fff;
  -webkit-overflow-scrolling: touch;
}
.findrecommend a {
  color: #666;
  text-decoration: none;
}
.findrecommend .swiper-container .swiper-wrapper .swiper-slide .image {
  width: 100%;
}
.findrecommend .swiper-container .swiper-wrapper .swiper-slide .type {
  position: absolute;
  right: 0;
  bottom: 20px;
  padding: 0 12px;
  height: 24px;
  line-height: 24px;
  color: #fff;
  font-size: 14px;
  font-weight: 400;
  border-radius: 12px 0 0 12px;
}
.findrecommend .swiper-container .swiper-pagination .swiper-pagination-bullet {
  margin: 0 4px;
}
.findrecommend .swiper-container .swiper-pagination .swiper-pagination-bullet.swiper-pagination-bullet-active {
  background: #c62f2f !important;
}
.findrecommend .recommend-type {
  width: 100%;
  height: 90px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.findrecommend .recommend-type .div-type {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 100%;
  width: 33.333333%;
  font-size: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0 10px;
}
.findrecommend .recommend-type .div-type .icon-type {
  display: inline-block;
  color: #c62f2f;
  font-size: 32px;
  width: 50px;
  height: 50px;
  line-height: 50px;
  text-align: center;
  border: 1px solid #c62f2f;
  border-radius: 50%;
}
.findrecommend .recommend-type .div-type .title {
  font-size: 12px;
  font-weight: 400;
  margin: 0;
  margin-top: 8px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  width: 100%;
  text-align: center;
}
.findrecommend .recommend-list {
  font-size: 0;
}


