
.findsheet {
  position: fixed;
  background: #fff;
  overflow: auto;
  top: 86px;
  bottom: 46px;
  left: 0;
  right: 0;
  padding: 15px;
  z-index: 11;
  background: #fff;
  -webkit-overflow-scrolling: touch;
}
.findsheet a {
  color: #666;
  text-decoration: none;
}


