
.findmusic {
  position: fixed;
  background: #fff;
  overflow: auto;
  top: 50px;
  bottom: 46px;
  left: 0;
  right: 0;
  z-index: 11;
  background: #f3f3f3;
  -webkit-overflow-scrolling: touch;
}
.findmusic .find-menu {
  position: fixed;
  height: 36px;
  width: 100%;
  line-height: 36px;
  background: #fff;
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.findmusic .find-menu .menu-list {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  width: 25%;
  overflow: hidden;
  text-align: center;
  cursor: pointer;
  font-size: 14px;
  position: relative;
  font-weight: 500;
}
.findmusic .find-menu .menu-list.router-link-active {
  color: #c62f2f;
}
.findmusic .find-menu .menu-list.router-link-active:after {
  content: '';
  position: absolute;
  background: #c62f2f;
  bottom: 0;
  height: 2px;
  left: 0;
  right: 0;
}
.findmusic .find-menu .menu-list:active {
  background: #d5d5d5;
}


