
.findsheettitle {
  box-sizing: border-box;
  height: 40px;
  line-height: 40px;
  position: relative;
}
.findsheettitle .name {
  display: inline-block;
  vertical-align: middle;
  height: 18px;
  line-height: 18px;
  position: relative;
  color: #333;
  font-size: 16px;
  font-weight: 400;
  padding-left: 12px;
}
.findsheettitle .name:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 3px;
  background: #c62f2f;
}
.findsheettitle .icon {
  display: inline-block;
  font-size: 16px;
  color: #999;
  vertical-align: middle;
}


